@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}body{font-family:"Montserrat",sans-serif;scroll-behavior:smooth}
.header{display:flex;justify-content:space-between;min-height:45px;position:fixed;top:0;width:100%;background-color:#000;padding-top:30px}.navbar a{color:#fff;margin-right:100px;text-decoration:none;font-size:13px;letter-spacing:3px;padding:30px 20px 28px 20px}.navbar a:hover{background-color:#333}.logo{font-size:15px;padding-left:50px;color:#fff;letter-spacing:.7rem}.burger-btn{outline:none;border:none;font-size:17px;color:#fff;font-weight:700;background:none;cursor:pointer;display:none}@media(max-width: 1100px){.burger-btn{display:block !important;position:absolute;right:50px;top:30px}.header{display:flex;flex-direction:column;align-items:center}.navbar{display:flex;flex-direction:column;visibility:hidden;height:0}.logo{padding-left:0px}.showHeight{height:auto;visibility:visible}}@media(max-width: 600px){.header{align-items:flex-start}.logo{width:50%;padding:2px 0px 20px 30px;line-height:22px}}
.main-section{background-image:url(/static/media/background-img.585d0033.jpg);background-repeat:no-repeat;background-size:cover;background-position:center center;height:100vh;width:100%;color:#f5f5f5}.main-section h1{padding-top:330px;padding-left:190px;font-size:80px;font-weight:500}.main-section p{padding:10px 190px;font-size:30px;letter-spacing:.5rem;text-transform:uppercase}@media(max-width: 1100px){.main-section h1{padding-left:50px;font-size:60px}.main-section p{padding-left:50px;font-size:25px}}@media(max-width: 600px){.main-section h1{font-size:40px}.main-section p{font-size:20px;line-height:23px}}
.projects-section{margin:50px}.projects-section h2{text-align:center;font-size:30px;font-weight:700;letter-spacing:7px;margin-bottom:60px}.project{display:flex;width:80%;margin:auto;box-shadow:rgba(149,157,165,.2) 0px 8px 24px;padding:20px}.project img{max-width:100%;height:auto;image-resolution:from-image 300dpi}.project .project-img{width:100%;margin:20px}.project .project-p{margin:20px;width:100%}.project .project-p p{line-height:20px;margin-bottom:20px}.project .project-p h4{margin-bottom:30px;font-weight:700}.project .project-p button{margin-right:20px;width:110px;padding:10px;border-radius:25px;outline:none;background-color:#000;color:#fff;border:none;cursor:pointer;font-weight:700;letter-spacing:1px}.project .project-p button a{text-decoration:none;color:#fff}.project .project-p .source-btn{background:none;color:#000;border:1px solid #000}.project .project-p .source-btn a{color:#000}h3{text-align:center;font-size:23px;font-weight:700;letter-spacing:10px;margin:70px auto;padding-bottom:10px;border-bottom-left-radius:20px;border-top-right-radius:20px;text-decoration:underline;-webkit-text-decoration-color:#0f0f0f;text-decoration-color:#0f0f0f}@media(max-width: 1200px){.project{flex-wrap:wrap}}@media(max-width: 850px){.projects-section h3{font-size:16px;width:300px}}@media(max-width: 400px){.projects-section h3{font-size:14px;width:200px}}
.footer{background-color:#f1f1f1;margin-top:150px}.footer .footer-row{display:flex;justify-content:center;margin:auto;width:80%;height:85vh}.footer .footer-text{font-size:12px;letter-spacing:2px;padding:40px;text-align:center}.footer-input{width:55%;box-shadow:0 0 10px rgba(0,0,0,.5);display:flex;flex-direction:column;justify-content:center;text-align:center;color:#000}.footer-input input,.footer-input textarea{width:70%;margin:20px;height:50px;border:none;background:none;border-bottom:1px solid #000;outline:none;color:#000;transition:all .3s cubic-bezier(0.64, 0.09, 0.08, 1);background:linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, black 4%);background-position:-800px 0;background-size:800px 100%;background-repeat:no-repeat;color:#000}.footer-input input:focus,.footer-input input:valid,.footer-input textarea:focus,.footer-input textarea:valid{box-shadow:none;outline:none;background-position:0 0}.footer-input input:focus::-webkit-input-placeholder,.footer-input input:valid::-webkit-input-placeholder,.footer-input textarea:focus::-webkit-input-placeholder,.footer-input textarea:valid::-webkit-input-placeholder{color:#000;font-size:13px;font-weight:700;-webkit-transform:translateY(-20px);transform:translateY(-20px);visibility:visible !important}.footer-input .footer-btn button{height:50px;width:150px;border:none;outline:none;cursor:pointer;background-color:#222;color:#f5f5f5;font-weight:700;font-size:15px;letter-spacing:3px;border-radius:5px;margin-top:30px;transition:all .5s ease-in-out}.footer-input .footer-btn:hover button{background:#fff;border:3px solid #222;color:#222;font-size:17px}.footer-input p{font-size:20px;margin-bottom:10px;color:#222}.footer-input h6{color:#222;font-size:14px;margin-bottom:50px}.footer-links{width:30%;background-color:#222;padding-top:230px;color:#f5f5f5;padding-left:50px}.footer-links h2{font-size:25px;font-weight:700;padding-bottom:30px;letter-spacing:3px}.footer-links a{color:#fff;margin-right:20px;font-size:23px;text-decoration:none;transition:.3s linear}.footer-links a:hover{color:#413b3b}.footer-links .email-div p,.footer-links .phone-div p{font-size:15px;line-height:20px;letter-spacing:2px}.footer-links .email-div :last-child,.footer-links .phone-div :last-child{padding-bottom:30px}@media(max-width: 1200px){.footer-row{flex-wrap:wrap;width:90%}.footer-row .footer-input{width:80%}.footer-row .footer-input p{padding-top:20px}.footer-row .footer-input button{margin-bottom:20px}.footer-row .footer-input input{margin:10px}.footer-row .footer-links{width:75%;padding-top:30px}}@media(max-width: 850px){.footer-links{padding-left:30px}.footer-links h2{font-size:19px}.footer-links .email-div p,.footer-links .phone-div p{font-size:12px;letter-spacing:1px}.footer-links .email-div :last-child,.footer-links .phone-div :last-child{padding-bottom:15px}.footer-text{display:none}}
